import { Model } from '@vuex-orm/core';
import Dua from "@/models/Dua";
import UnitPlanActivityDua from "@/models/UnitPlanActivityDua";
import UnitPlan from "@/models/UnitPlan";
import UnitPlanActivityOa from "@/models/UnitPlanActivityOa";
import Oa from "@/models/Oa";
import UnitPlanActivityFile from "@/models/UnitPlanActivityFile";

export default class UnitPlanActivity extends Model {
    static entity = 'unitplan_activities';

    static fields () {
        return {
            id: this.increment(),
            unitplan_id: this.number(null),
            name: this.string(''),
            disabled: this.boolean(false),
            created_at: this.string(new Date().toISOString().substr(0, 10)),
            update_at: this.string(new Date().toISOString().substr(0, 10)),

            //
            unitplan: this.belongsTo(UnitPlan, 'unitplan_id'),
            oas: this.belongsToMany(Oa, UnitPlanActivityOa, 'activity_id', 'oa_id'),
            files: this.hasMany(UnitPlanActivityFile, 'activity_id'),
            duas: this.belongsToMany(Dua, UnitPlanActivityDua, 'activity_id', 'dua_id'),
        };
    }
}
