import { Model } from '@vuex-orm/core';
import Ie from '@/models/Ie';

export default class Oa extends Model {
    static entity = 'oas';

    static fields () {
        return {
            id: this.increment(),
            cod: this.string(''),

            // compatibilidad
            selectedRows: this.attr([]), // IEs

            disabled: this.boolean(false),
            name: this.string(''),
            ies: this.hasMany(Ie, 'oa_id'),
        };
    }
};
