import { Model } from '@vuex-orm/core';
import UnitPlan from '@/models/UnitPlan';

export default class School extends Model {
    static entity = 'schools';

    static fields () {
        return {
            id: this.number(null),
            cod: this.number(null),
            name: this.string(''),
            logo: this.string('').nullable(),
            unitplans: this.hasMany(UnitPlan, 'school_id'),
            // unitplans: this.belongsToMany(UnitPlan, SchoolUnitPlan, 'school_id', 'unitplan_id'),
        };
    }
};
