import { Model } from '@vuex-orm/core';
import Oa from '@/models/Oa';

export default class Ie extends Model {
    static entity = 'ies';

    static fields () {
        return {
            id: this.increment(),
            oa_id: this.number(0),
            cod: this.string(''),
            name: this.string(''),
            rescheduled: this.number(0),
            disabled: this.boolean(false),
            created_at: this.string(new Date().toISOString().substr(0, 10)),
            update_at: this.string(new Date().toISOString().substr(0, 10)),

            oa: this.belongsTo(Oa, 'oa_id'),
        };
    }
};
