import { Model } from '@vuex-orm/core';
import UnitPlan from '@/models/UnitPlan';
import Oa from '@/models/Oa';

export default class UnitPlanOa extends Model {
    static entity = 'unitplan_oas';

    static fields () {
        return {
            id: this.increment(),
            oa_id: this.number(null),
            unitplan_id: this.number(null),
            complement: this.boolean(false), // Indica si es oa de complemento
            hours: this.string('00:00'),
            eje_id: this.number(0),
            rescheduled: this.number(null),
            disabled: this.boolean(false),
            created_at: this.string(new Date().toISOString().substr(0, 10)),
            update_at: this.string(new Date().toISOString().substr(0, 10)),

            // compatibilidad
            selectedRows: this.attr([]), // OAS
            editing: this.boolean(false),

            //
            unitplan: this.belongsTo(UnitPlan, 'unitplan_id'),
            oa: this.hasOne(Oa, 'id'),
        };
    }
}
