import { Model } from '@vuex-orm/core';
import Oa from '@/models/Oa';
import UnitPlanOa from '@/models/UnitPlanOa';
import School from '@/models/School';
import UnitPlanAttitude from '@/models/UnitPlanAttitude';
import UnitPlanPreLearning from '@/models/UnitPlanPreLearning';
import UnitPlanSkill from '@/models/UnitPlanSkill';
import Attitude from '@/models/masters/Attitude';
import PreLearning from '@/models/masters/PreLearning';
import Skill from '@/models/masters/Skill';
import UnitPlanActivity from '@/models/UnitPlanActivity';
import UnitPlanWeek from '@/models/UnitPlanWeek';

export default class UnitPlan extends Model {
    static entity = 'unit_plans';

    static fields () {
        return {
            id: this.increment(),
            school_id: this.number(0),
            id_course: this.attr(''), // a, b, c
            name: this.string('Unidad'),
            name2: this.string(''),
            description: this.string(''),
            hours: this.string('00:00'),
            startDate: this.string(new Date().toISOString().substr(0, 10)),
            endDate: this.string(new Date().toISOString().substr(0, 10)),
            id_level: this.number(0),
            year: this.number(new Date().getFullYear()), // period
            description: this.string('').nullable(), // Proposito

            // TODO: Solo por compatibilidad
            state: this.number(0),
            subject: this.attr(''),
            schoolLevel: this.attr(''),
            level: this.attr(''),
            course: this.attr(''),

            //
            school: this.belongsTo(School, 'school_id'),
            oas: this.belongsToMany(Oa, UnitPlanOa, 'unitplan_id', 'oa_id'),
            details: this.hasMany(UnitPlanOa, 'unitplan_id'), // Detalles del oa
            coverage: this.hasMany(UnitPlanWeek, 'unitplan_id'),

            //Purposes
            attitudes: this.belongsToMany(Attitude, UnitPlanAttitude, 'unitplan_id', 'attitude_id'),
            preLearnings: this.belongsToMany(PreLearning, UnitPlanPreLearning, 'unitplan_id', 'preLearning_id'),
            skills: this.belongsToMany(Skill, UnitPlanSkill, 'unitplan_id', 'skill_id'),

            // Activities
            activities: this.hasMany(UnitPlanActivity, 'unitplan_id'),
        };
    }

    get duration() {
        let h= 0;
        let m = 0;
        const details = UnitPlanOa.
                            query().
                            where('unitplan_id', this.id).
                            where('disabled', false).
                            get();

        const hours = details.map(item => item.hours);
        for (let i = 0; i < hours.length; i++) {
            let arr = hours[i].split(':');
            h += parseInt(arr[0]);
            m += parseInt(arr[1]);
            h += Math.floor(m / 60) ;
            m = m % 60;
        }

        if (isNaN(h) && isNaN(m)) {
            h = "00";
            m = "00";
        }

        return h + 'h:' + m + 'm';
    }

    get oa_count() {
        return UnitPlanOa.query().where('unitplan_id', this.id).where('disabled', false).count();
    }

    get activity_count() {
        return UnitPlanActivity.query().where('unitplan_id', this.id).count();
    }
}
